.App {
  display: flex;
  height: 100vh;
}

.App-left, .App-right {
  flex: 1;
  padding: 20px;
}

.App-left {
  background-color: #f0f0f0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

textarea.form-control {
  width: 100%;
  height: 90%;
  padding: 10px;
  border-radius: 8px;
}

.submit-button {
  font-size: 24px;
  padding: 10px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  position: fixed; /* Fixe le bouton */
  top: 20px; /* Positionne le bouton à 20px du haut */
  left: 20px; /* Positionne le bouton à 20px du côté gauche*/
  z-index: 10; /* S'assure que le bouton reste au-dessus des autres éléments */
}

.App-right {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 20px;
}

textarea.form-control {
  width: 100%;
  margin-bottom: 10px;
}

#llm-select {
  width: 100%;
}

.submit-button:hover {
  background-color: #45a049;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
